import { ScullyRoutesService, ScullyRoute } from '@scullyio/ng-lib';
import { fromEvent, Observable } from 'rxjs';

import { ChangeDetectorRef, Component, HostBinding, HostListener, Input, ViewChild } from '@angular/core';
import { Route } from '@angular/compiler/src/core';
import { ActivatedRoute, NavigationEnd, NavigationStart, Router, Routes } from '@angular/router';
import { map } from 'rxjs/internal/operators/map';
import { debounceTime, filter, share } from 'rxjs/operators';
import { MAIN_ROUTES } from '../share/app.constants';
import { AppStateService } from './app.state.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  activeLink;
  innerWidth;
  innerHeight;

  containerW;
  containerH;

  isPostScreen = false;

  @Input()
  trigger: string;

  bgStyle = {};

  constructor(private router: Router, private route: ActivatedRoute, private appState: AppStateService, private cdRef: ChangeDetectorRef) {

    this.innerWidth = window.innerWidth;
    this.innerHeight = window.innerHeight;
    this.calculateWH();
    const self = this;
    fromEvent(window, 'resize').pipe(debounceTime(500))
      .subscribe((event) => {
        this.calculateWH();
      });
    this.appState.getCurrentUrl$.subscribe(link => this.checkIsPostScr(link))
  }

  calculateWH() {
    this.innerWidth = window.innerWidth;
    this.innerHeight = window.innerHeight;
    if (this.innerWidth < 1230) this.innerWidth = 1230;
    if (this.innerHeight < 683) this.innerHeight = 683;
    this.setWH();
  }
  setWH() {
    // this.bgStyle = {};

    let width = this.innerWidth - 100;
    let height = (this.innerWidth / 1.8) - 100;

    if (height > this.innerHeight) {
      width = (this.innerHeight * 1.8) - 100;
      height = this.innerHeight - 100;
    }
    this.containerH = height;
    this.containerW = width;
    this.bgStyle['width.px'] = this.containerW;

    if (!this.isPostScreen) {
      this.bgStyle['height.px'] = this.containerH;
      this.bgStyle['min-height.px'] = this.containerH;
    } else {
      delete this.bgStyle['height.px'];
    }
  }
  ngOnInit() {

  }
  navigatePage(pageUrl: string) {
    this.activeLink = pageUrl;
    this.router.navigateByUrl(pageUrl);
  }

  // This fucntion born cause the <route-outlet> is dynamic from url, if check from NavigateEvent => make conflict
  checkIsPostScr(link: string) {
    const self = this;
    self.isPostScreen = false;
    if (/home/.test(link)) {
      setTimeout(() => {
        if (this.router.url === '/home') {
          self.navigatePage("/about-me");
        }
      }, 2000);
    } else if (/post/.test(link)) {
      self.isPostScreen = true;
    }
    self.calculateWH();
    console.log(self.isPostScreen);
  }
}
