import { Injectable } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { BehaviorSubject } from "rxjs/internal/BehaviorSubject";
import { map } from "rxjs/operators";

export interface StateModel {
    currentUrl?: string;
}

@Injectable({ providedIn: "root" })
export class AppStateService {

    private state = new BehaviorSubject<StateModel>(null);

    constructor(private router: Router, private route: ActivatedRoute) {
        this.state.next({ currentUrl: router.url })
    }

    accessRoute(link?: string) {
        if (!link) return;
        console.log('link', link);
        if (link) {
            this.state.next({ currentUrl: link });
        } else {
            this.state.next({ currentUrl: this.router.url });
        }
    }

    get getCurrentUrl$() {
        return this.state.pipe(map(stateModel => stateModel.currentUrl));
    }
}
