import { Component, EventEmitter, Output } from '@angular/core';
import { Route, Router } from '@angular/router';
import { filter } from 'rxjs/operators';
import { AppStateService } from 'src/app/app.state.service';
import { MAIN_ROUTES } from '../app.constants';

@Component({
    selector: 'app-sys-nav',
    templateUrl: 'sysnav.component.html',
    styleUrls: ['sysnav.component.scss']
})
export class SysNavComponent {
    links$: Route[] = MAIN_ROUTES;
    activeLink;
    @Output() switchTab = new EventEmitter<string>();

    constructor(private router: Router, private appState: AppStateService) {
        this.appState.getCurrentUrl$.pipe(filter(url => url !== this.activeLink)).subscribe(url => {
            this.activeLink = url;
        });
    }
    navToScreen(link: string) {
        this.router.navigateByUrl(link);
    }
}
