import { NgModule } from '@angular/core';
import { Routes, RouterModule, ActivatedRouteSnapshot, Router } from '@angular/router';
import { FooterModule } from '../share/footer/footer.module'
import { AppComponent } from './app.component';

import { Injectable } from '@angular/core';
import { CanActivate, CanActivateChild } from '@angular/router';
import { AppStateService } from './app.state.service';

@Injectable({ providedIn: "root" })
export class StateGuard implements CanActivate {
  constructor(private router: Router, private appState: AppStateService) {
  }

  canActivate(route: ActivatedRouteSnapshot) {
    //ask if he really wants to route.
    this.appState.accessRoute(route.url.map(item => item.path).join('/'));
    return true;
  }
}

export const MAIN_ROUTES: Routes = [
  { path: '', data: { title: '' }, redirectTo: 'home', pathMatch: "full" },
  { path: 'home', canActivate: [StateGuard], data: { title: 'Home' }, loadChildren: () => import('./../app/home/home.module').then(m => m.HomeModule) },
  { path: 'about-me', canActivate: [StateGuard], data: { title: 'About-me' }, loadChildren: () => import('./../app/aboutme/aboutme.module').then(m => m.AboutmeModule) },
  { path: 'experience', canActivate: [StateGuard], data: { title: 'Experience' }, loadChildren: () => import('./../app//exp/exp.module').then(m => m.ExpModule) },
  { path: 'portfolio', canActivate: [StateGuard], data: { title: 'Portfolio' }, loadChildren: () => import('./../app//portfolio/portfolio.module').then(m => m.PortfolioModule) },
  { path: 'post', canActivate: [StateGuard], data: { title: 'Post' }, loadChildren: () => import('./../app/mana/mana.module').then(m => m.ManaModule) }
];

@NgModule({
  imports: [RouterModule.forRoot(MAIN_ROUTES)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
